@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap');

:root {
  --pri: rgb(0, 177, 0);
  --i: 1;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.login-body {
  min-width: 420px;
   width: 100%;
  background: url("./images/image2.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  /* overflow-x: hidden; */
}

/* ----------login-component------- */

#login {
  width: 100%;
  /* border: 1px solid red; */
  display: flex;
  justify-content: center;
  align-items: center;
  /* flex-wrap: wrap; */
  padding: 20px;
}



/* ----------right-div/robo-hand----- */

#robo-hand {
  border: 5px solid var(--pri);
  height: 85vh;
  width: 470px;
  min-width: 380px;
  border-radius: 10px;
  background: url("./images/image1.jpg");
  background-repeat: repeat;
  background-position: center center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-weight: lighter;
  letter-spacing: 1px;
  flex-direction: column;
  line-height: 30px;
}

#robo-hand p {
  font-size: small;
  font-weight: bold;
}

/* ----------Both-btn----------- */

#robo-hand button,
#form-div button {
  padding: 10px;
  width: 120px;
  height: 40px;
  font-weight: bold;
  background-color: var(--pri);
  border: none;
  color: #fff;
  border-radius: 5px;
  margin-top: 25px;
  cursor: pointer;
  transition: all 0.2s ease;
  outline: none;
}

#form-div button {
  line-height: 10px;
}

#robo-hand button:hover,
#form-div button:hover {
  background-color: #fff;
  color: var(--pri);
  border: 1px solid var(--pri);
  box-shadow: 2px 4px #888888;
}

#robo-hand button:active,
#form-div button:active {
  background-color: var(--pri);
  color: #fff;
  box-shadow: -4px -2px #888888;
}

#form-div button i {
  font-size: 1.1rem;
  margin-right: 5px;
}

/* ------------INPUT-Section--------- */

#form {
  /* border: 1px solid blue; */
  height: 70vh;
  max-width: 400px;
  background: rgb(234, 255, 228);
  border-radius: 7px;
  z-index: 2;
  position: relative;
  left: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#form-div {
  /* border: 1px solid red; */
  height: 70%;
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

#form-div h4 {
  /* border: 1px solid brown; */
  display: flex;
  align-items: center;
}

#form-div h4 span {
  display: inline-block;
  width: max-content;
  /* border: 1px solid yellow; */
  margin: 3px;
  cursor: pointer;
}

#form-div span div {
  width: 20px;
  height: 20px;
  border: 4px solid var(--pri);
  background-color: var(--pri);
  color: #fff;
}

#form-div div {
  border: none;
  border-left: 3px solid var(--pri);
  height: 8vh;
  width: 100%;
  border-radius: 4px;
  background: #fff;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

#form-div div i {
  font-size: 150%;
  color: rgb(184, 184, 184);
}

#form-div div input {
  width: 250px;

  height: 50%;
  outline: none;
  border: none;
  border-left: 1px solid rgb(184, 184, 184);
  padding: 5px;
  font-style: italic;
}

#forgot-div {
  background-color: transparent !important;
  flex-direction: row;
  border: none !important;
  align-items: center !important;
  justify-content: space-between !important;
}

#form-div div input[type="checkbox"] {
  accent-color: rgb(0, 161, 0);
  cursor: pointer;
}
#form-div div input[type="checkbox"]:hover {
  accent-color: rgb(0, 161, 0);
}

label {
  min-width: 150px;
  font-size: 14px;
  margin-left: 5px
}

h4,
a {
  width: 1500px !important;
  font-size: small;
  text-align: right;
  color: black;
}

#form h1 {
  width: max-content;
  margin: 0 auto;
}

/* ----------------forgotpasswor----------- */

.forgotpassword {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.forgotpassword .container {
  background-color: aqua;
  width: 20%;
  height: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.forgotpassword button {
  padding: 10px 30px;
  cursor: pointer;
  border-radius: 5px;
  border: none;
  background-color: rgb(104, 173, 0);
  font-weight: bolder;
  color: #fff;
  z-index: 3;
}

.forgotpassword button:hover {
  color: rgb(104, 173, 0);
  background-color: #4a7331;
}

@media only screen and (max-width:868px) {
  #login {
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }

  #form {
    position: static;
    margin: 30px 0px !important;
    width: 380px;
  }

  #robo-hand {
    max-width: 80%;
  }
}